.button {
    width: 133px;
    height: 30px;
    border-radius: 3px;

}

.disabled {
    width: 133px;
    height: 30px;
    gap: 10px;
    background-color: #004D99;
    opacity: 0.5;
    border-radius: 3px;
}

.secondary {
    width: 167px;
    height: 30px;
    gap: 10px;
    background-color: #E5F0FA;
    border: 1px solid #0066CC;
    border-radius: 3px;
}

.default {
    width: 167px;
    height: 30px;
    border-radius: 3px;
    gap: 10px;
    background-color: #0066CC;
}

.red {
    width: 167px;
    height: 30px;
    background-color: #CC1439;
    gap: 10px;
}

.default:hover {
}

.red:hover {
}

.secondary:hover {
}


.default:active {
}

.secondary:active {
}

.red:active {
    background: #CC1439;
    border-radius: 3px;
}

